    function changeNews () {
      var $currentNews = $newsLists.filter('.show');
      var $nextNews = $currentNews.next();

      if ($nextNews.length === 0) {
        $nextNews = $newsLists.eq(0);
      }
      $newsLists.removeClass('show');
      $nextNews.addClass('show');
    }
    $('.news-change').on('click', function () {
      changeNews();
    });
  },
